import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faOctopusDeploy} from '@fortawesome/free-brands-svg-icons'
import './NavBar.css'

class NavBar extends Component {
  render() {
    return (
      <nav  class="navbar navbar-expand-sm bg-dark navbar-dark">
        <div class="container-fluid">
          <Link style={{ textDecoration: 'none', color: 'white' }} to="/"><a class="navbar-brand" href="#"><FontAwesomeIcon icon={faOctopusDeploy}/>&nbsp;Diaz Orlandi</a></Link>
          <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavbar">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse" id="collapsibleNavbar">
            <ul class="navbar-nav">
              <li class="nav-item">
                <Link style={{ textDecoration: 'none', color: 'white' }} to="/notocar"><a class="nav-link" href="/notocar">No tocar</a></Link>
              </li>
              <li class="nav-item">
                <Link style={{ textDecoration: 'none', color: 'white' }} to="/proximamente"><a class="nav-link" href="/proximamente">Próximamente</a></Link>
              </li>
              <li class="nav-item">
                <Link style={{ textDecoration: 'none', color: 'white' }} to="/tampocotocar"><a class="nav-link" href="/tampocotocar">Tampoco tocar</a></Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}

export default NavBar;