import React, { Component } from 'react';
import "../Estilos/EstilosBase.css";
import "./Home.css"
import profilePicture from "../../Assets/Img/profilePictureCompress.jpg"


class Home extends Component {
    render() {
        return (
            <header className='home'>
                
                <div className='centradoHome'>
                    <div className='HomeStyle'>
                        <img src={profilePicture} alt="" className='profilePictureStyle' />
                        <div className='containerInfoName'>
                            <div className='titleHome'>
                                ¡Hola! Me llamo
                            </div>
                            <div className='NameHome'>
                                Diaz Orlandi, Ignacio Emanuel
                            </div>
                        </div>
                    </div>
                </div>
 
            </header>

        );
    }
}

export default Home;