import logo from './logo.svg';
import './App.css';
import NavBar from './Components/NavBar/NavBar';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Ventana1 from './Components/Ventana1/Ventana1';
import Ventana2 from './Components/Ventana2/Ventana2';
import Ventana3 from './Components/Ventana3/Ventana3';
import Home from './Components/Home/Home';
import ParticleBackground from './Components/Particulas/Particulas.js';
import InfoHome from './Components/InfoHome/InfoHome';


function App() {
  return (
    <Router>
      <NavBar/>
      <Switch>
      <Route path="/" exact>
        <div className='RouterStyle'>
          <ParticleBackground/>
          <Home/>
          <InfoHome/>
          </div>
        </Route>
        <Route path="/notocar">
          <Ventana1/>
        </Route>
        <Route path="/proximamente">
          <Ventana2/>
        </Route>
        <Route path="/tampocotocar">
          <Ventana3/>
        </Route>
      </Switch>
    </Router>

  );
}

export default App;
